html,
body {
    font-family: "Roboto", "Verdana", sans-serif;
    font-weight: 400;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: $color-main-light-2;
}

h1,
h2 {
    font-weight: 300;
    text-align: center;
    user-select: none;
}

h1 {
    @include colorGradient($color-gradient-1);
    font-size: 3.5rem;
    // margin: 0 auto 1rem auto;
}

h2 {
    @include colorGradient($color-gradient-2);
    font-size: 2.3rem;
}

p {
    text-align: justify;
}

.title {
    @include flexColumnCenter;
    width: 100%;

    &::after {
        content: "";
        display: block;
        height: 0.2rem;
        width: 20%;
        margin: 2rem auto;
        background-image: $color-gradient-3;

        @include respond(600) {
            margin: 3.5rem;
        }
    }
}

label {
    @include colorGradient($color-gradient-1);
    user-select: none;
}
